
import { defineComponent, ref } from "@vue/runtime-core";
import { useStore } from "vuex";

export default defineComponent({
  setup() {
    const store = useStore();
    function searchInApi(search: any) {
      store.commit("searchInApi", search);
    }
    return {
      searchInApi,
      heroNameSearch: ref(""),
    };
  },
});
